
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

export default {
directives: {
TransferDom
},
props: {
title: String,
options: Array,
change: {
type: Function,
default: () => {}
},
value: {
type:String,
default:''
},
haha: {
type:String,
default:''
},
type: {
type:String,
default:'radio'
},
placeholder: {
type:String,
default:''
},
pleaseChooseShow:{
type:Boolean,
default:false,
},
deepValue: {
},
deepValue1: {
},
},
watch: {
newValue(val){
console.log('val', val)

},
newValue1(val){
console.log('val1', val)

},





},
created(){

},
data () {
return {
newValue: this.deepValue || [],
newValue1: this.deepValue1,
show: false,
label:'',
selectedColor: '#4D7BFE'
}
},
methods: {
open(){
this.show = true
},
radioOnChange(value, label) {
if (label instanceof Array) {
label = label.join(' 、');
}
this.label = label;

this.$emit('change',value,this.label)
this.show = false;
},
onChange(value, label){
if (label instanceof Array) {
label = label.join(' 、');
}
this.label = label;

this.$emit('change',value,this.label)
},
clickRight(){
this.show = false;
}

}
}



