
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

export default {
directives: {
TransferDom
},
props: {
title: String,
options: Array,
change: {
type: Function,
default: () => {}
},
value: {
type:String,
default:''
},
pleaseChooseShow:{
type:Boolean,
default:false,
}
},
watch: {
value(val){
this.label = val
}
},
created(){
this.label=this.value;
},
data () {
return {
newValue: this.value,
label: '',
show: false
}
},
methods: {
open(){
this.show = true
},
onChange(value, label){
this.label = label;
this.change(value, label);
this.show = false;
}
}
}



