import Header from '@/pages/common/header'
export default {
    name:'redeemResult',
    components:{
        Header
    },
  data () {
    return {
      redeemStatus:this.$route.params.redeemStatus,
    }
  },
  methods:{
    leave:function(path){
      this.$router.push(path)
    }

  }

