import Header from '@/pages/common/header'
import Footer from '@/pages/common/footer'
export default {
  name: 'contractAdd',
  components: {
    Header,
    Footer
  },
  data () {
    return {
      id: this.$route.query.id,
      formData: {
        sex: '',
        birthday: '',
        certificate: '',
        certificateDate: ''
      }
    }
  },
  methods: {
      //保存
      save(){
        let VM = this
        this.$vux.alert.show({
          title: '提示',
          content: '添加成功！',
          onHide(){
            VM.$router.push({path: '/personal/contractAddSuccess'})
          }
        })
      }
    }
  }
  


