export default {
    props: {
      customerCapitalList: {
          type: Array,
          default: []
        },
    },
    data () {
      return {
        showMore:true
      }
    },
  created(){
    this.customerCapitalList.forEach(item =>{
      for(var key in item){
        if(item.hasOwnProperty(key)){
          if(key === 'amount'){
            if(!item[key]){
              item[key] = '--'
              return false;
            }
            item[key] =  this.utils.formatData(item[key]);
          }
        }
      }
    })

  },
  methods:{
    goTo(pinCode){
      this.$router.push({ path: '/customer/assetsList',query:{pinCode:pinCode}});
    }

  }
}



