import Header from '@/pages/common/header';
import loadingImg from '@/assets/images/loading.gif';
export default {
  name: 'uploadAttachment',
  data() {
    return {
      showFace: false,
      size: 0,
      limit: 5, //限制图片上传的数量
      customerId:this.$route.query.customerId,
      visitId:this.$route.query.visitId,
      attachList:this.$store.state.recordDetail.attachList,
      loadingImg:loadingImg,
      loadingLi:false,//loading的li
    }
  },
  components: {
    Header
  },
  methods: {
    chooseType() {
      document.getElementById('upload_file').click();
    },
    fileChange(el) {
      //获取信息
      if (!el.target.files[0].size) return;
      this.fileList(el.target);
      el.target.value = ''
    },
    fileList(fileList) {
      let files = fileList.files;
      for (let i = 0; i < files.length; i++) {
        //判断是否为文件夹
        if (files[i].type != '') {
          this.fileAdd(files[i]);
        } else {
          //文件夹处理
          this.folders(fileList.items[i]);
        }
      }
    },
    //文件夹处理
    folders(files) {
      let _this = this;
      //判断是否为原生file
      if (files.kind) {
        files = files.webkitGetAsEntry();
      }
      files.createReader().readEntries(function (file) {
        for (let i = 0; i < file.length; i++) {
          if (file[i].isFile) {
            _this.foldersAdd(file[i]);
          } else {
            _this.folders(file[i]);
          }
        }
      });

    },
    foldersAdd(entry) {
      let _this = this;
      entry.file(function (file) {
        _this.fileAdd(file)
      })
    },
    fileAdd(file) {
      //上传数量不能超过5张
      if (this.attachList.length==this.limit) {
        this.utils.msg("上传数量不能超过5个")
        return;
      }
      //当这个文件上传大于10M的话，就超出上传范围
      if(file.size>10240000){
        this.utils.msg("上传图片大于10M,请重新上传");
        return;
      }
      var that=this;
      //判断是否为图片文件
      if (file.type.indexOf('image') == -1) {
        this.utils.msg("请选择图片文件");
      } else {
        this.loadingLi=true;
        let reader = new FileReader();
        let image = new Image();
        let _this = this;
        reader.readAsDataURL(file);
        reader.onload = function () {
          //给file添加src
          file.src = this.result;
          image.src = this.result;
          image.onload = function () {
            let width = image.width;
            let height = image.height;
            file.width = width;
            file.height = height;
            //ajax传送到后台
            var formData = new FormData();
            formData.append("file",file);
            $.ajax({
              url:'/djjf-web/fileUpload/upload',
              type:"post",
              data:formData,
              processData: false,
              contentType:false,
              success:function(res){
                that.loadingLi=false;
                if(res.code==200){
                  let fileTmp=res.data;
                  fileTmp.module='customerVisit';
                  that.attachList.push(fileTmp);
                }else{
                  this.utils.msg("上传失败,请重试");
                }
              },
              error:function(){
                that.loadingLi=false;
                this.utils.msg("上传失败,请重试");
              }
            })
          };
        }
      }
    },
    delImg(index) {
      this.attachList.splice(index,1);
    },
    save() {
     //编辑保存了之后返回到上一页
     this.$store.commit("changeRecordDetail",{'attachList':this.attachList});
     this.$router.go(-1);
    }
  },

}



